<template>
  <div class="">
    <input type="text" class="w-75 my-40" placeholder="請輸入待辦事項" v-model.trim="todo" @keyup.enter="submitHandler">
  </div>
</template>

<script>
export default {
  name:'todo-input',
  data(){
    return{
      todo:'',
    }
  },
  methods:{
    submitHandler(){
      if(!this.todo) return 
      this.$store.commit('ADD_Todo',{
        content:this.todo,
        complete:false,
        timestamp:Date.parse(new Date()),
      });
      console.log(this.todo);
      this.todo='';
    }
  }
}
</script>