<template>
  <div class="row mb-2">
       <input type="text" v-model.trim="edit"  class="editInput" v-if="edit !== null"  v-focus @keyup.enter="submitHandler" @keyup.esc="cancelHandler" @blur="cancelHandler"   >

    <template v-else>
      <div class="col-lg-3 col-12 my-lg-20 my-10 timestamp">
        {{timestamp}}
      </div>
      <div class="col-lg-6 col-10 my-lg-20 my-10">
        <input  type="checkbox" v-model="complete" >
        <label for="" @dblclick="editHandler">{{todo.content}}</label>
      </div>
      <div class="col-lg-1 col-2  close my-lg-20 my-10" style="font-size:1.5rem;cursor:pointer" @click="deleteHandler">×</div> 
      <div class=" col-lg-2 col-10 timediff my-lg-20 my-10">{{timediff}}</div>
    </template>
  </div>
</template>



<script>

export default {
 name:'todolist-item',
 data(){
   return{ 
     edit:null,
     timestampdiff:null
   }
 },
 props:{
   index:{
     type:Number,
     required:true
   }
 },
 computed:{
   todo(){
       console.log('refresh');
       let item = this.$store.state.todos;
       return item[this.index]
   },
   complete:{
     get(){
       console.log('get',this.todo)
       return this.todo.complete;
     },
     set(val){
       console.log(val);
       this.$store.commit('UPDATE_Todo',{
         index:this.index,
         data:{ 
           content:this.todo.content,
           complete:val,
         }
       })
     }
   },
   timestamp(){
     let timestamp = this.todo.timestamp / 1000;
     this.timestampdiff = timestamp;
     let d = new Date();
     let week = ['(日)','(一)','(二)','(三)','(四)','(五)','(六)'];
     d.setTime(this.todo.timestamp);
     let dm = (d.getMinutes()<10?'0':'') + d.getMinutes();
     let dh = (d.getHours()<10?'0':'') + d.getHours();

     return d.getFullYear()+'-'+(d.getMonth()+1) + '-' + d.getDate() + '-' +week[d.getDay()] + " - " +dh+ ":" + dm
   },
   timediff(){
     let d = Date.parse(new Date());
     d = d / 1000;
     let timediff = d - this.timestampdiff;
     switch(true)
     {
       case timediff < 60:
       return '剛剛';
       
       case timediff > 60 && timediff < 3600:
       return  Math.floor( (timediff) / 60) + "mins ago" ;
       
       case timediff > 3600 && timediff < 3600*24:
       return  Math.floor( (timediff) / 3600) + "hours ago" ; 
       
       case timediff > 3600*24 && timediff < 3600*24*30:
       return  Math.floor( (timediff) / 3600*24) + "days ago" ;
       
       case timediff >3600*24*30:
       return  Math.floor( (timediff) / 3600*24*30) + "days ago";
       
       default :
       return 'he'
     }
   }
 },
 methods:{
   deleteHandler(){
     if(confirm('是否確認刪除'+this.todo.content +'?')){
      this.$store.commit('DELETE_Todo',this.index);
     }
   },
   editHandler(){
     console.log('a')
     this.edit = this.todo.content;
   },
   submitHandler(){
     const cahce = this.todo.content;
     if(!this.edit) {
       this.deleteHandler();
       this.edit = cahce
      }
     this.$store.commit('UPDATE_Todo',{
         index:this.index,
         data:{ 
           content:this.edit,
           complete:this.todo.complete,
         }
       })
       this.cancelHandler();
   },
   cancelHandler(){
     this.edit = null;
   },
 },
 directives:{
   focus:{
     inserted:function(el){
       el.focus()
     }
   }
 }
}

</script>


<style lang="scss" scoped>
.editInput{
  width: 100%;
}
input[type='checkbox']:checked + label{
 color: rgba(0,0,0,.5);
 text-decoration: line-through;
 font-style: italic;
}
label{
  margin-bottom: 0px;
  padding-left: 50px;
}
.isComplete{
  color:rgba(0,0,0,.5);
  text-decoration:line-through;
}
.row{
  background: #fff;
  padding: 6px 10px;
  border-radius: 4px;
  box-shadow: 5px 4px 3px rgba(0,0,0,.1);
  div{
    display: flex;
    position: relative;
  }
}
.close{
  transition: all .2s;
  &:hover{
    color: red;
  }
}
input:focus{
  outline: 0;
}
input[type="checkbox"]{
     -webkit-appearance:none;
     width: 30px; 
     height: 30px;
     border: 1px solid #ccc;
     background: #fff;
     border-radius: 4px;
     box-shadow: inset 0 1px 3px rgba(0,0,0,.1);
     position: relative;
}
input[type="checkbox"]::after{
  pointer-events: none;
  opacity: 0;
  transform: scale(0);
  content: '\2713\0020';
  position: absolute;
  font-size: 1.5em;
  top: 48%;
  left: 65%;
  margin-top:-50%;
  margin-left: -50%;
  color: red;
  transition: all .2s;
  font-family: 'Lucida Sans Unicode', 'Arial Unicode MS', Arial;
}
input[type="checkbox"]:checked::after{
  opacity: 1;
  transform: scale(1);
}

  .timestamp , .timediff{
    letter-spacing: 2px
  }

</style>