import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const LS = {

  load(){
    return JSON.parse((localStorage.getItem('vue-todos')) || '[]')
  },
  save(data){
    localStorage.setItem('vue-todos',JSON.stringify(data));
  }
};

const filter = {
  all(todos){
    return todos
  },
  active(todos){
    return todos.filter( todo => !todo.complete )
  },
  complete(todos){
    return todos.filter( todo => todo.complete )
  }
}
export default new Vuex.Store({
  // strict:true,
  state: {
    todos:[
      {complete:false,content:'hello',timestamp:null},
    ]
  }, 
  getters:{
    todoIndex(state){
        return filter[state.route.name](state.todos).map(item=> state.todos.indexOf(item));
    }
  },
  mutations: {
    SET_Todos(state,data){
    // console.log( state.todos )
     state.todos = data;
     LS.save(state.todos);
    },
    ADD_Todo(state,data){
      // console.log(state.todos , data)
      state.todos.push(data);
      LS.save(state.todos);
    },
    UPDATE_Todo(state,{index,data}){
      // console.log(index);
      // console.log(data);

      state.todos[index].complete = data.complete;
      state.todos[index].content = data.content;
      LS.save(state.todos);
    },
    DELETE_Todo(state,index){
      state.todos.splice(index,1) ;
      LS.save(state.todos);
    }
  },
  actions: {
    INIT_Todos({commit}){
      commit('SET_Todos',LS.load());
    }
  }
})
